@import url(https://fonts.googleapis.com/css?family=Teko);
body {
  background: #282c34;
  color: white;
  margin: 0;
  padding: 0;
  font-family: 'Teko', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

.App-header {
  box-sizing: border-box;
  background-color: #282c34;
  height: 100px;
  font-size: 60px;
  color: cyan;
  text-align: left;
  padding: 20px 50px; }

.App-link {
  color: #61dafb; }

.scoreboard {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center; }
  .scoreboard .scoreboard-box {
    box-sizing: border-box;
    width: calc(20% - 80px);
    border: solid cyan 4px;
    padding: 80px;
    margin: 50px;
    text-align: center;
    opacity: 0;
    -webkit-animation: fade-in 0.5s ease-in-out 0s 1 forwards;
            animation: fade-in 0.5s ease-in-out 0s 1 forwards; }
  .scoreboard .teamboard {
    min-width: 250px;
    min-height: 500px; }
    .scoreboard .teamboard .name {
      font-size: 40px;
      line-height: 1;
      margin-bottom: 0.5em; }
    .scoreboard .teamboard .score {
      font-size: 200px;
      line-height: 0.75;
      text-shadow: 0.03em 0.03em red; }
    .scoreboard .teamboard .buttons {
      white-space: nowrap; }
      .scoreboard .teamboard .buttons .button {
        display: inline-block;
        width: 20px;
        height: 20px;
        line-height: 20px;
        margin: 10px;
        font-size: 20px;
        font-weight: bold;
        font-family: Arial, Helvetica, sans-serif;
        padding: 0.5em;
        border-radius: 50%;
        background: white;
        color: #282c34;
        cursor: pointer; }
  .scoreboard .new-team {
    width: auto;
    padding: 20px;
    border-radius: 20px;
    font-size: 100px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold; }
    .scoreboard .new-team input {
      display: block;
      background: transparent;
      color: white;
      font-family: Arial, Helvetica, sans-serif;
      font-size: 20px;
      border: solid 2px white;
      padding: 5px;
      visibility: hidden; }
      .scoreboard .new-team input.active {
        visibility: visible; }

@-webkit-keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

